
// import { Button } from "@mui/material";
import DuelsModal from "./DuelsModal";
import { useState, useEffect} from "react";
import axios from "axios";
import { Button } from "@mui/material";
export default function DuelsDashboard(props) {

    const [showModal, setShowModal] = useState(false)
    const [opponentData, setOpponentData] = useState([])

    useEffect(()=> {
        // don't fetch opponent info if user is not logged in / user prop is not ready
        if (!props.user) {
            return
        }

        axios.get("/players").then(
            res => {
                
               let opponents = []
               let players = res.data
               let currUser = props.user
    

            players.forEach((player) => {
                if (player.username !== currUser.username) {
                    opponents.push(player)
                }
            })

    
                setOpponentData(opponents)
        
        
            }
        ).catch(
            err => {
    
                console.log(err)
      
            }
        )
    
      },[props.user])

    return (

        <div className="duels-dashboard-container">
            <div className="duels-header-container">
            <h2 style={{color: "white", fontWeight: "1000", fontSize: "40px", paddingTop: "25px",paddingBottom: "25px", letterSpacing: "2px"}}>Duels</h2>
            </div>

            {props.isLoggedIn?<>
            <button onClick={()=>{setShowModal(true)}}>Challenge a Duel</button>
            <DuelsModal user={props.user} opponentData={opponentData} show={showModal} setShow={setShowModal}/>
            </>:""}
        
        </div>

    )
}



