import Modal from 'react-bootstrap/Modal';
import { useState, useEffect } from 'react';
import { Button } from '@mui/material';
import Select from 'react-select'
import axios from 'axios';
// import 'bootstrap/dist/css/bootstrap.css';
export default function DuelsModal(props) {

    const [options, setOptions] = useState([])
    const [selectedOpponent, setSelectedOpponent] = useState()
    const [showPotentialEloChanges, setShowPotentialEloChanges] = useState(false)
    const [userPotentialLoss, setUserPotentialLoss] = useState()
    const [userPotentialGain, setUserPotentialGain] = useState()

    function handleClose() {
        setUserPotentialGain("")
        setUserPotentialLoss("")
        setShowPotentialEloChanges(false)
        setSelectedOpponent("")
        props.setShow(false)
    }


    useEffect(()=> {

        
        let selections = []
                    
        props.opponentData.forEach((player) => {
            
            let option = {
                value: player,
                label: player.name + " (" + player.elo + ")"
            }
            selections.push(option)
            
        })
        /**
         *                 players.sort(function(a,b) {
                    return b.elo - a.elo
                })
         */
        selections.sort(function(a,b) {
            return b.value.elo - a.value.elo
        })
    
        setOptions(selections)
    
    },[props.opponentData])

    function calculateLossPotential(eloDifference) {
        let maxLoss = 30
        // need to round down as integer
        let calculatedLoss = ((5000)/(eloDifference)) 
        return Math.floor(Math.min(maxLoss, calculatedLoss))
    }

    function calaculateGainPotential(eloDifference) {
        let maxGain = 150
        let minGain = 30
        let calculatedGain = Math.min(150, Math.max(minGain,0.3*(eloDifference))) 
        return Math.floor(calculatedGain)

    }


    function calculatePotentialEloChanges(user, opponent) {
        // user's potential gain == opp's potential loss
        // user's potential loss == opp's potential gain

        let eloDiff = Math.abs(user.elo - opponent.elo)
        let lowerEloPlayerPotentialLoss = calculateLossPotential(eloDiff)
        let lowerEloPlayerPotentialGain = calaculateGainPotential(eloDiff)

        if (user.elo < opponent.elo) {
            // user is the lower elo player
            setUserPotentialLoss(lowerEloPlayerPotentialLoss)
            setUserPotentialGain(lowerEloPlayerPotentialGain)
        } else {
            // user is the higher elo player
            setUserPotentialGain(lowerEloPlayerPotentialLoss)
            setUserPotentialLoss(lowerEloPlayerPotentialGain)
        }

    }

    useEffect(() => {
        if (!selectedOpponent) {
            // don't show potential elo gain/loss
            setShowPotentialEloChanges(false)
        } else {
            // calculate potential elo gain loss for both parties
            // show changes
            calculatePotentialEloChanges(props.user, selectedOpponent.value)
            setShowPotentialEloChanges(true)
        }


    },[selectedOpponent])

    
    
    





    return (
        <Modal
        className='duels-modal'
        show={props.show}
        onHide={handleClose}
        keyboard={false}
        size='xl'
        backdrop='static'>

            <Modal.Header className='modal-header'closeButton>
            <Modal.Title >
                
                <h3>New Duel</h3>
            </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                    
                    <label  className='modal-label'>Opponent</label>

                     <Select

                        name="opponent-select"
                        options={options}
                        className="opponent-select"
                        closeMenuOnSelect={true}
                        placeholder='Choose an opponent..'
                        value={selectedOpponent}
                        onChange={(choice) => setSelectedOpponent(choice)}
                        />

            {showPotentialEloChanges?
            <div className='potential-elo-changes-container'>
                <div className='modal-padding'></div>
                <div>
                    <label className='modal-label'>Your potential Gain</label>
                    <input className='elo-input' type='text' disabled={true} value={"+ " + userPotentialGain}/>

                    <div className='modal-padding'></div>

                    <label className='modal-label'>Your potential Loss</label>
                    <input className='elo-input' type='text' disabled={true} value={"- " + userPotentialLoss}/>
                </div>
                <div className='modal-padding'></div>
                <div>
                    <label className='modal-label'>Opponent's potential Gain</label>
                    <input className='elo-input' type='text' disabled={true} value={"+ " + userPotentialLoss}/>

                    <div className='modal-padding'></div>

                    <label className='modal-label'>Opponent's potential Loss</label>
                    <input className='elo-input' type='text' disabled={true} value={"- " + userPotentialGain}/>
                </div>

            </div>
            :""}

            </Modal.Body>

            <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
                Cancel
            </Button>
            <Button variant="primary">Confirm</Button>
            </Modal.Footer>
    </Modal>
    )
}